@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 18px;
  font-family: 'Montserrat', sans-serif;
}

.top-bar-social {
  position: fixed;
  top: 36px;
  left: 36px;
  font-size: 3rem;
  z-index: 1000;
}

.top-bar-social a {
  color: red;
}

.reg-btn {
  font-size: 2rem;
  color: #fff;
  padding: 5px 20px;
  font-variant: small-caps;
  border: none;
  cursor: pointer;
  border-radius: 30px;
  background: red;
  animation: btn-animation 4s infinite;
}

.top-bar-btn {
  margin-bottom: 7vw;
}

.bottom-btn {
  margin-bottom: 2rem;
}

.reg-btn:hover {
  animation: btn-beat 1s infinite;
}

@keyframes btn-animation {
  5%,
  50% {
    transform: scale(1);
  }
  10% {
    transform: scale(0.95);
  }
  15% {
    transform: scale(1.05);
  }
  20% {
    transform: scale(1.05) rotate(-5deg);
  }
  25% {
    transform: scale(1.05) rotate(5deg);
  }
  30% {
    transform: scale(1.05) rotate(-3deg);
  }
  35% {
    transform: scale(1.05) rotate(2deg);
  }
  40% {
    transform: scale(1.05) rotate(0);
  }
}

@keyframes btn-beat {
  50% {
    transform: scale(1.1);
  }
}

.footer {
  height: 100px;
  background: linear-gradient(90deg, black, red);
  margin-top: -100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #eee;
  padding: 3rem;
}

.wrapper {
  background: fixed center no-repeat url('./images/test/bg.jpg');
}

.container {
  min-height: 100vh;
  padding-bottom: 100px;
  background-color: rgba(0, 0, 0, 0.6);
}

.top-bg {
  background: fixed no-repeat url('./images/test/top-bg.png');
  background-size: 100% auto;
}

.top-fg {
  min-height: calc(100vw * 0.5625);
  background: no-repeat url('./images/test/top-fg.png');
  background-size: 100% auto;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
}

.sim-screen {
  height: 900px;
  background: center no-repeat url('./images/test/mid-screen.jpg');
  clip-path: polygon(100% 0, 100% 90%, 50% 100%, 0 90%, 0 0);
}

.sim-info {
  height: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  font-size: 2rem;
}

.sim-info-top {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.rules-and-prizes {
  margin: 2rem 0;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.rules {
  counter-reset: li;
  list-style: none;
  *list-style: decimal;
  padding: 0;
  
  margin: 2rem 2rem 2rem 3rem;
}

.rules span {
  position: relative;
  display: block;
  padding: 0.4em 0.4em 0.4em 2em;
  *padding: 0.4em;
  margin: 0.8em 0;
  background: rgba(255, 255, 255, 0.8);
  color: #333;
  text-decoration: none;
  border-radius: 0.3em;
  transition: all 0.3s ease-out;
}

.rules span:hover {
  background: #eee;
}

.rules span:hover::before {
  transform: rotate(360deg);
}

.rules span::before {
  content: counter(li);
  counter-increment: li;
  position: absolute;
  left: -1.3em;
  top: 50%;
  margin-top: -1.3em;
  background: #612121;
  height: 2em;
  width: 2em;
  color: #fff;
  line-height: 2em;
  border: 0.3em solid #fff;
  text-align: center;
  font-weight: bold;
  border-radius: 2em;
  transition: all 0.3s ease-out;
}

.prizes {
  padding: 2rem;
  margin-right: 2rem;
  min-width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  border-radius: 20px;
  text-align: center;
  border: 4px solid #fff;
}

.prizes h2 {
  font-size: 1.4rem;
}

.prizes h3 {
  width: 100%;
  margin-top: 1rem;
  padding-top: 0.5rem;
  margin-bottom: 0.5rem;
  border-top: 2px solid #fff;
}

.flex-line {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.classes {
  height: 900px;
  background: center no-repeat url('./images/test/class_t.jpg');
  background-size: auto;
  clip-path: polygon(100% 20%, 100% 90%, 50% 100%, 0 90%, 0 0);

  font-size: 2rem;
  color: #fff;
  font-variant: small-caps;
}

.qual {
  height: 100%;
  background: no-repeat url('./images/test/qual.png');
  background-position-x: center;
  background-position-y: 5rem;

  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
}

.qual span {
  text-align: center;
  max-width: 460px;
  margin-bottom: 4rem;
}

.sim-bg {
  margin-top: -100px;
  background: fixed center url('./images/test/sim-bg.jpg');
}

.sim {
  margin: 0 1rem;
  padding-top: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  font-variant: small-caps;
  font-size: 1.5rem;
}

.sim p {
  max-width: 800px;
  text-align: center;
  padding-bottom: 1rem;
}

@media (max-width: 1200px) {
  .top-bar-btn {
    font-size: 1.5rem;
  }
  
  .rules-and-prizes {
    flex-direction: column;
  }

  .rules {
    margin: 0 1rem 0 2rem;
    font-size: 16px;
  }

  .prizes {
    margin: 1rem 0 0;
  }
}

@media (max-width: 800px) {
  .top-bg {
    background-position-y: 1rem;
    margin-top: 1rem;
  }

  .top-bar-btn {
    margin-bottom: 4vw;
  }

  .sim-screen {
    height: 600px;
    background-size: 200%;
  }

  .classes {
    height: 600px;
    font-size: 1.5rem;
  }

  .sim-info {
    font-size: 1.5rem;
  }

  .sim-info-top {
    margin-top: 1rem;
  }

  .qual {
    background-size: 100%;
  }
}

@media (max-width: 600px) {
  .top-bg {
    background-position-y: 2rem;
    margin-top: 2rem;
  }

  .top-bar-btn {
    font-size: 1rem;
    margin-bottom: 1rem;
  }

  .rules {
    margin: 0 .5rem 0 .5rem;
  }

  .prizes {
    min-width: 350px;
    margin-left: -1rem;
  }

  .sim-screen {
    height: 400px;
    background-size: 200%;
  }

  .rules-and-prizes {
    flex-direction: column;
    margin: 2rem .5rem 2rem 1.5rem;
  }

  .sim-info {
    height: 85%;
    font-size: 1rem;
  }

  .classes {
    height: 400px;
    background-size: 200%;
    font-size: 1rem;
  }

  .sim {
    padding-top: 120px;
    font-size: 18px;
    
  }
}
